<template>
	<section>
		<!---->
		<el-collapse v-model="activeNames">
			<el-collapse-item title="" name="1">
                <el-form ref="form" :model="form" label-width="80px" @submit.prevent="onSubmit" style="margin:10px;width:60%;min-width:600px;">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-select v-model="form.deviceNo" placeholder="">
                                    <!--<el-option label="" value="all"></el-option>-->
                                    <!--<el-option label="" value="1"></el-option>-->
                                    <!--<el-option label="" value="2"></el-option>-->
                                    <el-option v-for="item in form.deviceNoItems" :key="item" v-bind:value="item.value">{{item.text}}</el-option>
                                </el-select>
                                <p>:{{form.deviceNo}}</p>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label-width="80px" label="">
                                <el-date-picker v-model="form.startTime" type="datetime" format="yyyy-MM-dd HH:mm:ss" placeholder="">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-form-item label="">
                        <el-checkbox-group v-model="form.repeat">
                            <el-checkbox label="" name="repeat"></el-checkbox>
                            <el-checkbox label="" name="repeat"></el-checkbox>
                            <el-checkbox label="" name="repeat"></el-checkbox>
                            <el-checkbox label="" name="repeat"></el-checkbox>
                            <el-checkbox label="" name="repeat"></el-checkbox>
                            <p>{{form.repeat.join('|')}}</p>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-checkbox-group v-model="form.type">
                                    <el-checkbox label="" name="type"></el-checkbox>
                                    <el-checkbox label="" name="type"></el-checkbox>
                                    <el-checkbox label="" name="type"></el-checkbox>
                                </el-checkbox-group>
                                <p>{{form.type.join('|')}}</p>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <!--<el-form-item label="">-->
                            <!--<el-checkbox-group v-model="form.state">-->
                            <!--<el-checkbox label="" name="type"></el-checkbox>-->
                            <!--<el-checkbox label="" name="type"></el-checkbox>-->
                            <!--</el-checkbox-group>-->
                            <!--<p>{{form.state.join('|')}}</p>-->
                            <!--</el-form-item>-->
                            <el-form-item label="">
                                <el-radio-group v-model="form.state">
                                    <el-radio class="radio" :label="1"></el-radio>
                                    <el-radio class="radio" :label="0"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-form :model="filters">
                        <el-col :span="6">
                            <el-form-item style="margin-left: 10px;">
                                <el-input v-model="filters.siteName" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="4">
                            <el-form-item style="margin-left: 30px;">
                                <el-button type="primary" v-on:click="getUsers"></el-button>
                            </el-form-item>
                        </el-col>
                    </el-form>
                </el-form>
			</el-collapse-item>
		</el-collapse>

        <div class="panel">
            <div class="panel-title">
                <span v-text="panelTitle"></span>
                <div class="fr">
                    <el-button @click.stop="on_refresh" size="small">
                        <i class="fa fa-refresh"></i>
                    </el-button>
                    <el-button type="primary" @click="handleAdd" icon="plus" size="small"></el-button>
                </div>
            </div>
            <div class="panel-body">
                <!---->
                <el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
                    <el-table-column type="selection" width="55">
                    </el-table-column>
                    <el-table-column type="index" width="60">
                    </el-table-column>
                    <el-table-column prop="siteName" label="" width="120">
                    </el-table-column>
                    <el-table-column prop="deviceNo" label="" width="100" sortable>
                    </el-table-column>
                    <el-table-column prop="startTime" label="" width="120" sortable>
                    </el-table-column>
                    <el-table-column prop="repeat" label="" min-width="120">
                    </el-table-column>
                    <el-table-column prop="state" label="" min-width="120" :formatter="formatState">
                    </el-table-column>
                    <el-table-column prop="type" label="" min-width="100">
                    </el-table-column>
                    <el-table-column label="" width="150">
                        <template scope="scope">
                            <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                            <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>

                <!---->
                <el-col :span="24" class="toolbar">
                    <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" class="fl"></el-button>
                    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.curPage" :page-sizes="[10,20,30, 50]" :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" class="fr">
                    </el-pagination>
                </el-col>
            </div>
        </div>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="siteName">
					<el-input v-model="editForm.siteName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.deviceNo" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.startTime"></el-date-picker>
				</el-form-item>
                <!--<el-form-item label="">-->
                    <!--<el-input type="textarea" v-model="editForm.repeat"></el-input>-->
                <!--</el-form-item>-->
				<el-form-item label="">
					<el-checkbox-group v-model="editForm.repeat">
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
				<el-row>
					<el-col :span="12">
						<el-form-item label="">
							<el-checkbox-group v-model="editForm.type">
								<el-checkbox label="" name="type"></el-checkbox>
								<el-checkbox label="" name="type"></el-checkbox>
								<el-checkbox label="" name="type"></el-checkbox>
							</el-checkbox-group>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<!--<el-form-item label="">-->
							<!--<el-checkbox-group v-model="editForm.state">-->
								<!--<el-checkbox label="" name="type"></el-checkbox>-->
								<!--<el-checkbox label="" name="type"></el-checkbox>-->
							<!--</el-checkbox-group>-->
						<!--</el-form-item>-->
						<el-form-item label="">
							<el-radio-group v-model="editForm.state">
								<el-radio class="radio" :label="1"></el-radio>
								<el-radio class="radio" :label="0"></el-radio>
							</el-radio-group>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form ref="addForm" :model="addForm" label-width="80px" :rules="addFormRules">
				<el-form-item label="" prop="siteName">
					<el-input v-model="addForm.siteName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.deviceNo" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="addForm.startTime"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-checkbox-group v-model="addForm.repeat">
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
						<el-checkbox label="" name="repeat"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
				<el-row>
					<el-col :span="12">
						<el-form-item label="">
							<el-checkbox-group v-model="addForm.type">
								<el-checkbox label="" name="type"></el-checkbox>
								<el-checkbox label="" name="type"></el-checkbox>
								<el-checkbox label="" name="type"></el-checkbox>
							</el-checkbox-group>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<!--<el-form-item label="">-->
							<!--<el-checkbox-group v-model="addForm.state">-->
								<!--<el-checkbox label="" name="type"></el-checkbox>-->
								<!--<el-checkbox label="" name="type"></el-checkbox>-->
							<!--</el-checkbox-group>-->
						<!--</el-form-item>-->
						<el-form-item label="">
							<el-radio-group v-model="editForm.state">
								<el-radio class="radio" :label="1"></el-radio>
								<el-radio class="radio" :label="0"></el-radio>
							</el-radio-group>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    siteName: ''
                },
                panelTitle: '',
                users: [],
                total: 0,
                hello: true,
                listLoading: false,
                activeNames: ['1'],
                sels: [],//

                form: {
                    siteName: '',
                    deviceNo: '',
                    deviceNoItems: [{text:'',value:''},{text:'',value:''},{text:'',value:''}],
                    startTime: '',
					repeat: [],
                    state: 0,
                    type: ['',''],
                    desc: ''
                },

                listQuery: {
                    curPage: 1,
                    limit: 20,
                    pageSize: 10,
                    sort: '+id'
                },

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                editForm: {
                    id: 0,
                    siteName: '',
                    deviceNo: '',
                    startTime: '',
                    repeat: [],
                    state: 0,
                    type: []
                },

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                //
                addForm: {
                    siteName: '',
                    deviceNo: '',
                    startTime: '',
                    repeat: [],
                    state: [],
                    type: ['','']
                }

            }
        },
        methods: {
            //
            formatState: function (row, column) {
                return row.state == 0 ? '' : row.state == 1 ? '' : '';
            },
            //
            handleSizeChange(val) {
                this.listQuery.pageSize = val;
                this.getUsers();
            },
            handleCurrentChange(val) {
                this.listQuery.curPage = val;
                this.getUsers();
            },
            //
            on_refresh(){
                this.getUsers();
            },
            //
            getUsers() {
                let para = {
                    curPage: this.listQuery.curPage,
                    pageSize: this.listQuery.pageSize,
                    siteName: this.filters.siteName
                };
                this.listLoading = true;
                //NProgress.start();
                getUserListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.users = res.data.users;
                    for (var i = 0;  i < this.users.length; i++) {
                        if(this.users[i].type.length > 1){
                            var arr = [];
                            for (var j = 0;  j < this.users[i].type.length-1; j++) {
                                this.users[i].type[j] = this.users[i].type[j] + "|";
								arr.push(this.users[i].type[j]);
                            }
                            arr.push(this.users[i].type[this.users[i].type.length-1]);
                            this.users[i].type = arr;
						}
                    }
                    this.listLoading = false;
                    //NProgress.done();
                });
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { id: row.id };
                    removeUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
                let strs = []; //
				strs = this.editForm.type;
                this.editForm.type = [];
                for (let i = 0;  i < strs.length; i++) {
                    const str = strs[i];
                    this.editForm.type.push(str.substring(0,2));
                }
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
                this.addForm = {
                    siteName: '',
                    deviceNo: -1,
                    startTime: '',
                    repeat: [""],
                    state: [],
                    type: ["", ""]
                };
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
                            para.startTime = (!para.startTime || para.startTime == '') ? '' : util.formatDate.format(new Date(para.startTime), 'yyyy-MM-dd');
                            editUser(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            para.startTime = (!para.startTime || para.startTime == '') ? '' : util.formatDate.format(new Date(para.startTime), 'yyyy-MM-dd');
                            addUser(para).then((res) => {
                                this.addLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                console.log("type " + para.type);
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { ids: ids };
                    batchRemoveUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            }
        },
        mounted() {
            this.getUsers();
        }
    }

</script>

<style scoped>

</style>


